@import "../../styles/lavenes.scss";

.textbox {
    border: 1px solid rgba(0,0,0,.125);
    border-radius: .25rem;
    background-color: #fff;
    width: 100%;
    height: 44.31px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.620rem .75rem;
    box-sizing: border-box;
    transition: all .4s ease;
    margin-bottom: 1rem;

    &.textarea {
        transition: none;
        width: 100%;
        height: 123px;
        max-width: 100%;
        font-size: 1rem;
        font-family: "Open Sans";
        color: #495057;
    }

    &:focus-within {
        border-color: #4d77fa;
        box-shadow: 0 0px 8px rgba(77, 119, 250, .15);
    }

    & input {
        outline: none;
        border: none;
        color: #495057;
        width: 100%;
        font-size: 1rem;
        font-family: "Open Sans";
    }
}

.textbox-icon-textbox {
    padding: 0;
    display: flex;
    height: 43px;
    margin-bottom: 1rem;

    & .color-container {
        & input {
            background: rgba(0, 0, 0, 0.125);
            border: none;
            outline: none;
            //padding: 0;
            margin: 0;
            height: 100%;
            border-radius: .25rem;
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
            border-right: 0;
        }
    }
    
    & .icon-container {
        width: 42px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content:center;
        background-color: #e9ecef;
        border: 1px solid #ced4da;
        border-radius: .25rem;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
        border-right: 0;

        & .icon {
            color: #76839c;
            font-size: 18px;
        }
    }

    & .textbox {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
    }
}

textarea {
    outline :none;
    border: none;
}