@import "../../styles/lavenes.scss";

.list {
    &.social {
        padding: 0;
        margin: 0;
        display: flex;
        flex-wrap: wrap;
        list-style-type: none;

        & li {
            color: #76839c;
            border: 1px solid #eceef7;
            padding: 5px 5px 5px 5px;
            border-radius: 50%;
            width: 44px;
            height: 44px;
            cursor: pointer;
            float: left;
            margin: 0 8px 8px 0;
            font-size: 24px;
            display: flex;
            align-items: center;
            justify-content: center;
            box-sizing: border-box;

            &.active, &:hover {
                border: 1px solid #4d77fa;
                color: #4d77fa;
            }
        }
    }
}