@import "../../styles/lavenes.scss";

.background-image {
    width: 100%;
    height: 500px;
    position: relative;

    & .background-image-img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }

    & .background-color {
        width: 100%;
        height: 100%;
        background: #1A499A;
    }
}