@import "../../styles/lavenes.scss";

.card-preview {
    width: 100%;
    max-height: 567px;
    overflow: auto;

    & .background {
        width: 100%;
        height: 240px;
        background: red;
    }

    & .information-container {
        width: 100%;
        padding: 15px;
        border-radius: 8px;
        background: #608FFF;
        position: relative;
        box-sizing: border-box;
        margin-top: -30px;
        
        & .first-information {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin-top: -64px;

            & .avatar-container {
                overflow: hidden;
                border-radius: 50%;
                width: 100px;
                height: 100px;
                box-sizing: border-box; 
                padding: 3px;
                background: #fff;
                box-shadow: 0px 5px 10px 0px rgb(0 0 0 / 20%);
                
                & .avatar {
                    width: 100%;
                    height: 100%;
                    background: blue;
                    border-radius: 50%;
                }
            }

            & span.user-name {
                margin-top: 1.5rem;
                margin-bottom: .5rem;
                font-size: 24px;
                color: #fff;
            }

            & span.job {
                margin-bottom: 1.5rem;
                font-size: 14px;
                color: #fff;
            }
        }

        & .information-group {
            width: 100%;
            border-radius: 4px;
            border: solid 1px #ECEEF7;
            margin-top: .5rem!important;
            box-sizing: border-box;
            padding: .75rem 1.25rem;
            background: #fff;

            & .information-gr-header {
                display: flex;
                margin-bottom: .75rem;
                position: relative;

                & .left {
                    flex: 1;
                    color: #608FFF;
                    display: flex;
                    align-items: center;

                    & .icon {
                        font-size: 16px;
                    }

                    & span.title {
                        font-size: 18px;
                        margin-left: 12px;
                    }
                }

                & .right {
                    & .action-btn {
                        position: absolute;
                        right: 0;
                        top: 0;
                        color: #fff;
                        max-width: 107px;
                        border-color: #4d77fa;
                        background:  #608FFF;
                        box-shadow: 0 5px 11px 0 rgb(0 0 0 / 18%), 0 4px 15px 0 rgb(0 0 0 / 15%);
                        cursor: pointer;
                        padding: 7px 7px 7px 15px !important;
                        font-size: 12px;
                        border-radius: 60px;
                        display: flex;
                        box-shadow: 0 5px 11px 0 rgb(0 0 0 / 18%), 0 4px 15px 0 rgb(0 0 0 / 15%);

                        & span {
                            font-size: 12px;
                        }

                        & .circle-container {
                            width: 36px;
                            height: 36px;
                            border-radius: 50%;
                            background: #3e5da6;
                            flex-shrink: 0;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            font-size: 16px;
                            
                            & .icon {
                                color: #fff;
                            }
                        }
                    }
                }
            }

            & .information-input {
                width: 100%;
                border-bottom: solid 1px #ECEEF7;
                padding: 10px 0;

                & span.label {
                    width: 100%;
                    display: block;
                    font-size: 12px;
                }

                & span.value {
                    width: 100%;
                    display: block;
                    color: #000;
                    font-size: 15px;
                }

                &:last-child {
                    border: none;
                }
            }

            & .content {
                color: #808ea7;
                font-size: 15px;

                & a {
                    color: #608FFF;
                    text-decoration: none;
                    display: block;
                    margin-top: 12px;

                    &:hover {
                        text-decoration: underline;
                    }
                }
            }

            & .action-button-2 {
                width: 128px;
                height: 38px;
                font-size: 1rem;
                border: 1px solid #6c757d;
                color: #6c757d;
                margin-top: 1rem!important;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: .25rem;
                cursor: pointer;
                transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;

                & span {

                }

                &:hover {
                    background: #6c757d;
                    color: #fff;
                }
            }

            & .information-image {
                width: 100%;
                border-radius: 8px;
                box-shadow: 0px 5px 10px 0px rgb(0 0 0 / 20%);
                margin: 0 0 .5rem 0;

                &:last-child {
                    margin: 0;
                }
            }

            & .social-group {
                display: flex;
                align-items: center;
                padding: 10px 0;
                border-bottom: solid 1px #ECEEF7;
                text-decoration: none;

                & .circle-container {
                    width: 32px;
                    height: 32px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-right: .5rem!important;
                    background: #608FFF;
                    border-radius: 50%;
                    
                    & .icon {
                        color: #fff;
                        font-size: 14px;
                    }
                }

                & .name {
                    font-size: 15px;
                    color: #141A22;
                }

                &:last-child {
                    border: none;
                    padding-bottom: 0;
                }

                &:first-child {
                    padding-top: 0;
                }
            }
        }
    }
}