@import "../../styles/lavenes.scss";

nav.navbar {
    width: 100%;
    box-sizing: border-box;
    padding: 24px 95px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    z-index: 99999;

    @include mobile {
        padding: 8px 16px;
    }

    & .logo {
        width: 161px;
        height: 40px;
        background-size: cover;

        @include mobile {
            width: 140px;
            height: 35px;
        }
    }

    & ul {
        list-style-type: none;
        display: flex;
        flex: 1;
        align-items: center;
        justify-content: flex-end;

        & li {
            & a {
                &.btn {
                    width: 100px;
                    height: 36px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-radius: 4px;
                    border: 1px solid #fff;
                    text-decoration: none;
                    margin-left: 8px;
                    font-size: 14px;

                    @include mobile {
                        width: 80px;
                    }
                }

                &.login-btn {
                    background: #fff;
                    color: #1A499A;
                }

                &.signup-btn {
                    background: transparent;
                    color: #fff;

                }
            }
        }
    }
}