@import "../../styles/lavenes.scss";

.card {
    &.container-c {
        background-color: #fff;
        border-radius: .25rem;

         & .header {
            padding: 1.25rem;
            box-sizing: border-box;
            display: flex;
            align-items: center;
            justify-content: center;
            border-bottom: 1px solid rgba(0,0,0,.125);

             & .title {
                 font-size: 24px;
                 flex: 1;
                 margin: 0;
                 font-weight: 400;
             }
                 
             & .control-container {

             }
         }

         & .container {
             box-sizing: border-box;
             padding: 1.25rem;
         }
    }

    &.collapse-c {
        background-color: #fff;
        border-radius: .25rem;
        box-shadow: 1px 2px 5px #f9f9f9;
        border: 1px dotted #eceef7;
        overflow: hidden;

        & .header {
            padding: 1.25rem;
            box-sizing: border-box;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: #f0f0ff;
            border-bottom: 1px solid #eceef7;
            cursor: pointer;

            & .title {
                font-size: 16px;
                color: #666666;
                font-weight: 500;
                flex: 1;
            }

            & .control-container {
                & .control {
                    & .icon {
                        color: #000000;
                    }
                }
            }
        }

        & .container {
            padding: 1.25rem;
            box-sizing: border-box;
        }

        &.hide {
            height: 63px;
        }
    }
}