@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');
@import "./common/mixin";

label {
    display: inline-block;
    margin-bottom: .5rem;
    color: #76839c;
    font-size: 15px;
}

.container {
    width: 100%;
    max-width: 1140px;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    padding-top: 64px;

    @include mobile {
        width: calc(100% - 30px);
    }

    @include tablet {
        width: calc(100% - 30px);
    }
}

.border-top {
    border-top: 1px solid #eceef7;
}

.col-md{
    &-3 {
        -webkit-box-flex: 0;
        flex: 0 0 25%;
        max-width: 25%;
        position: relative;
        width: 100%;
        min-height: 1px;
        padding-right: 15px;
        padding-left: 15px;
        color: #76839c;
        font-size: 15px;
        box-sizing: border-box;

        @include mobile {
            max-width: 100%;
            margin-bottom: 16px;
        }

        @include tablet {
            max-width: 100%;
            margin-bottom: 16px;
        }
    }

    &-4 {
        -webkit-box-flex: 0;
        flex: 0 0 33.333333%;
        max-width: 33.333333%;
        position: relative;
        width: 100%;
        min-height: 1px;
        padding-right: 15px;
        padding-left: 15px;
        color: #76839c;
        font-size: 15px;
        box-sizing: border-box;

        @include mobile {
            max-width: 100%;
            padding-left: 15px!important;
        }

        @include tablet {
            max-width: 100%;
            padding-left: 15px!important;
        }
    }

    &-6 {
        -webkit-box-flex: 0;
        flex: 0 0 50%;
        max-width: 50%;
        position: relative;
        width: 100%;
        min-height: 1px;
        padding-right: 15px;
        padding-left: 15px;
        color: #76839c;
        font-size: 15px;
        box-sizing: border-box;

        @include mobile {
            max-width: 100%;
        }

        @include tablet {
            max-width: 100%;
        }
    }

    &-8 {
        -webkit-box-flex: 0;
        flex: 0 0 66.66667%;
        max-width: 66.66667%;
        position: relative;
        width: 100%;
        min-height: 1px;
        padding-right: 15px;
        padding-left: 15px;
        color: #76839c;
        font-size: 15px;
        box-sizing: border-box;
        
        @include mobile {
            max-width: 100%;
        }
        
        @include tablet {
            max-width: 100%;
        }
    }

    &-9 {
        -webkit-box-flex: 0;
        flex: 0 0 75%;
        max-width: 75%;
        position: relative;
        width: 100%;
        min-height: 1px;
        padding-right: 15px;
        padding-left: 15px;
        color: #76839c;
        font-size: 15px;
        box-sizing: border-box;
        
        @include mobile {
            max-width: 100%;
            //padding: 0;
        }
        
        @include tablet {
            max-width: 100%;
            //padding: 0;
        }
    }
}

.row {
    display: flex;
    flex-wrap: wrap;
    margin-left:-15px;
    margin-right:-15px;

    @include mobile {
        flex-direction: column;
    }

    @include tablet {
        flex-direction: column;
    }
}

.mt {
    &-3 {
        margin-top: 1rem!important;
    }

    &-4 {
        margin-top: 1.5rem!important;
    }
}

.pt {
    &-3 {
        padding-top: 1rem!important;
    }
}

.mb {
    &-3 {
        margin-bottom: 1rem!important;
    }
}

body, html {
    padding: 0;
    margin: 0;
    height: 100vh;
    font-family: 'Open Sans', sans-serif;
}