@import "../../styles/lavenes.scss";

.selection {
    & .color-box {
        width: 70px;
        height: 70px;
        border-radius: 50%;
        border: 1px solid #eceef7;
        position: relative;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        margin: 0 8px 8px 0;

        &:hover {
            border: 1px solid #4d77fa;
        }

        &.active {    
            border: 1px solid #4d77fa;
            
            & .color-container .checkbox {
                opacity: 1;
            }
        }

        & .color-container {
            width: 60px;
            height: 60px;
            border-radius: 50%;
            overflow: hidden;

            & .color-1 {
                width: 100%;
                height: 44px;
                background-color: #608FFF;
            }

            & .color-2 {
                width: 100%;
                height: 24px;
                background-color: #608FFF;
                opacity: 25%;
            }

            & .checkbox {
                border: solid 2px #fff;
                position: absolute;
                top: -1px;
                left: 0;
                width: 20px;
                height: 20px;
                background-color: #4d77fa;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 50%;
                opacity: 0;

                & .icon {
                    font-size: 14px;
                    color: #fff;
                }
            }
        }
    }

    & .color-menu {
        width: 100%;
        display: flex;
    }

    & .image-box {
        border: 1px solid #eceef7;
        width: 70px;
        height: 70px;
        margin: 0 8px 8px 0;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border-radius: 4px;
        background-color: #fff;
        position: relative;

        &:hover {
            border: 1px solid #4d77fa;
        }

        &.active {    
            border: 1px solid #4d77fa;
            
            & .image-container .checkbox {
                opacity: 1;
            }
        }

        & .image-container {
            width: 58px;
            height: 58px;

            & .checkbox {
                border: solid 2px #fff;
                position: absolute;
                top: -7px;
                left: -7px;
                width: 16px;
                height: 16px;
                background-color: #4d77fa;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 50%;
                opacity: 0;

                & .icon {
                    font-size: 14px;
                    color: #fff;
                }
            }

            & .image {
                width: 100%;
                height: 100%;
                background-size: cover;
                background-position: center;
                display: flex;
                align-items:center;
                justify-content: center;
                position: relative;

                &.action {
                    cursor: pointer;
                    background: #eceef7;
                }

                & .icon {
                    font-size: 28px;
                    color: #808ca4;
                    position: absolute;

                    &.hide {
                        opacity: 0;
                    }
                }

                & input {
                    display: none;
                }

                & label {
                    width: 100%;
                    height: 100%;
                    cursor: pointer;
                    z-index: 9;
                }
            }
        }
    }

    & .image-detail-box  {
        height: 92px;
        padding: 5px;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        margin-bottom: -1px;
        border: 1px solid rgba(0,0,0,.125);
        position: relative;

        &:first-child {
            border-top-left-radius: .25rem;
            border-top-right-radius: .25rem;
        }

        &:last-child {
            border-bottom-left-radius: .25rem;
            border-bottom-right-radius: .25rem;
        }

        &.active {

        }

        &:hover {
            &:first-child {
                & .control .action-btn:first-child {
                    opacity: 0;
                }
            }

            & .control .action-btn:first-child {
                opacity: 1;
            }
        }
        
        & .image {
            width: 80px;
            height: 80px;
            background-color: #414141;
            background-size: cover;
            background-position: center;
        }   


        & .detail {
            height: 100%;
            flex: 1;
            padding: 0.620rem .75rem;
            box-sizing: border-box;

            & .name {
                font-size: 12px;
                color: #495057;
            }
        }

        & .control {
            display: flex;
            height: 100%;

            & .action-btn {
                width: 38px;
                height: 32px;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: .25rem;
                margin: 5px 0 0 5px;
                transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
                cursor: pointer;
                background-color: #efefef;
                
                & .icon {
                    color: #76839c;
                }

                &:first-child {
                    opacity: 0;
                }
            }
        }
    }
}